import * as React from 'react';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';

function Roll1() {
   return (
      <ImageList className='roll'>
      {itemData.map((item) => (
        <ImageListItem key={item.img}>
          <img
            src={`${item.img}?w=500&fit=crop&auto=format`}
            srcSet={`${item.img}?w=500&fit=crop&auto=format&dpr=2 2x`}
            alt={item.title}
            className='roll-item'
            
            loading="lazy"
          />
          <ImageListItemBar
            title={item.title}
            position="below"
          />
        </ImageListItem>
      ))}
    </ImageList>
   )
}
export default Roll1;


const itemData = [
   {
     img: '000.png',
     title: 'Fast Mode Map'
   },
   {
     img: '001.png',
     title: 'Train Mode Map'
   },
   {
     img: '002.png',
     title: 'Monsters'
   },
   {
     img: '003.png',
     title: 'Monsters Attacking'
   },
   {
     img: '004.png',
     title: 'Full Game Map'
   },
   {
     img: '005.png',
     title: 'Cutting Trees'
   },
   {
     img: '006.png',
     title: 'Tool Selection'
   },
   {
     img: '007.png',
     title: 'Market System'
   }
 ];
 